<template>
  <div class="login-wrap" id="indexLizi">
    <div class="login-title">漫画管理系统</div>
<!-- 登录 -->
    <el-form :model="logform" :rules="loginRules" ref="logform"  class="login-container">
      <div class="title">登录</div>
      <el-form-item prop="name">
       <el-input type="text" placeholder="admin" :maxlength="20" size="large" v-model="logform.name" > </el-input>
      </el-form-item>
      <el-form-item prop="pwd">
       <el-input placeholder="●●●●●●" type="password" v-model="logform.pwd" size="large" :maxlength="20"> </el-input>
      </el-form-item >
      <el-form-item style="width:100%;" class="login-btn">
         <el-button type="primary" size="large" style="width:100%;" @click.native.prevent="login":loading="logining">{{logintext}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
    data() {
      var validatename = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入信息！'))
        } else {
          callback();
        }
      }
      return {
        logining: false,
        logintext: '登录',
        registertext: '注册',
        logform: {
          name: '',
          pwd:'',
        },
        loginRules: {
          name: [
            { required: true, validator: validatename, trigger: 'blur' }
          ],
          pwd: [
            { required: true, message: '请输入密码！', trigger: 'blur' },
            { min: 6, max: 16, message: 'must be over 6 numbers', trigger: 'blur' }
          ]
        }
      }
    },
  methods: {
    login() {
      var _this = this
      console.log(_this.logform)
      this.$refs.logform.validate((valid) => {
        if (valid) {
          _this.logining = true
          _this.logintext = "Wait..."
          this.$ajax.post('/api/bgLogin',
            _this.logform
            ).then(function(response){
              if(0 === response.data.code){
                _this.logintext = "Loging..."
                setTimeout(function () {
                _this.logining = false
                _this.$message({message: '登录成功',type: 'success'})
                sessionStorage.setItem('BIGDATA_PLATFORM', JSON.stringify(response.data.data))
                _this.$router.push({ path: '/Welcome' })}, 1200)
              } else if(1 === response.data.code){
                _this.$message({message: response.data.msg, type: 'error'})
              }
            }).catch(function(error){
              console.log(error) 
            })
        } else { return false}
        setTimeout(() => {
          _this.logining = false
          _this.logintext = "Enter"
        }, 3000)
      })
    }
  }
}

</script>

<style scoped>
.change{
  right:12px;
  top: 2px;
  color:#7bbaf4;
  position:absolute;
}
.login-title {
  text-align: center;
  padding: 50px 20px 30px;
  font-size: 32px;
  font-weight: 700;
  text-shadow: 0 2px 2px rgba(0,0,0, .4);
  letter-spacing: 3px;
  color: #fff;
  margin: 50px 0px 40px 0px;
}
.login-container {
  border-radius: 5px;
  width: 350px;
  max-width: 90%;
  padding: 20px 30px;
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
  margin: 0 auto;
  position: relative;
  z-index: 10;
  }
.login-wrap {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #193c6d;
  background-image: linear-gradient(-135deg, #5da2d7, #7bbaf4, #2aa2d7);
  overflow: hidden;
}
@media (max-width: 480px){
    padding:10px;
    width:auto;
    background: none;
    box-shadow: none;
    .title{
      display: none;
    } 
}
.title {
    text-align: center;
    color: #888;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 25px;
    
  }
.login-btn{
    /* padding-top: 10px; */
    margin-bottom: 12px;
}
.el-button--large{
   padding:15px;
   border-radius: 5px;
   background: #63B8FF;
   border-color:#63B8FF;
   color:#fff;
}
.el-button--large:hover {
   background: #5d99e6;
   border-color:#4d99e6;
}
</style>


